.mainDiv {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6rem;
  background-size: cover;
  padding: 60px;
  background-color: grey;
  position: relative;
  isolation: isolate;
  border-bottom: 40px solid grey;
}

.mainDiv::after {
  content: '';
  position: absolute;
  z-index: -1;
  background-color: pink;
  inset: 0;
  opacity: 80%;
  background-image: url('../images/pexels-adam-krypel-6498990.jpg');
}

.headerSpan {
  font-size: 17px;
  font-style: italic;
}

.headerH1 {
  font-size: 50px;
}

.darkModeSection {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 1rem;
}

.darkModeButton {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  width: 60px;
  height: 60px;
  font-size: 40px;
  cursor: pointer;
}

.bottomSection {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.aboutMeText {
  text-align: center;
  max-width: 900px;
}

.projectSection {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 1rem;
}

.projectCard {
  display: flex;
  padding: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgba(255, 255, 255, 0.226);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.projectPicture {
  width: 300px;
}

.projectLinks {
  font-size: 40px;
  display: flex;
  flex-direction: row;
  gap: 4rem;
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}

.formInput {
  height: 30px;
  max-width: 700px;
  border-radius: 100px;
  border: none;
  padding: 10px;
}

.formInput:focus {
  outline: none;
}

.textArea {
  border-radius: 10px;
  padding: 10px;
}

.textArea:focus {
  outline: none;
}

.submitButton {
  padding: 5px;
  cursor: pointer;
}

.profile-picture {
  width: 150px;
  border-radius: 10px;
}

.resume-section {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 3%;
}

.tech-header {
  text-decoration: underline;
}

.resume-left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.download-link {
  color: white;
}

.fade-in {
  opacity: 0;
  transition: opacity 5s;
}

.fade-in.visible {
  opacity: 1;
}

.footerSection {
  padding-top: 10px;
  padding-bottom: 10px;
}

@media (max-width: 665px) {
  .headerH1 {
    font-size: 40px;
  }
}
